.Cart{
    .Wrapper{
        padding: 20px;

        .Title{
            font-weight: 300;
            text-align: center;
            text-transform: capitalize;
        }
        .Top{
            display: flex;
            padding: 20px;
            .Topbutton{
                padding: 10px;
                font-weight: 600;margin-right: 10px;
                text-transform: capitalize;
                cursor: pointer;
                &:nth-child(3){
                    border: none;
                    background-color: #111;
                    color: #fff;
                }
            }
            .Toptexts{
                .toptext{
                    cursor: pointer;
                    margin: 0 10px;
                }
            }
            align-items: center;justify-content: space-between;
        }
        
        .Bottom{
            display: flex;
            justify-content: space-between;
            .Info{
flex: 3;
            }
            .summary{
                position: sticky;
                top: 0;
              flex: 1;
              border: 0.5px solid lightgray;
              border-radius: 10px;
              padding: 20px;
              height: 50vh;
              .Title{font-weight: 200;}
              .summaryitem{
                  margin: 30px 0;
                  display: flex;
                  text-transform: capitalize;
                  justify-content: space-between;
                  .summarytext{
                     
                  }
                  .summarytext.total,
                  .summaryprice.total{
                    font-weight: 700;
                    font-size: 25px;

                  }
                  

              }
              .sumarybtn{
width: 100%;padding: 10px;font-size: 18px;
text-transform: uppercase;
background-color: #111;
color: white;
border: none;cursor: pointer;
font-weight: 600;
              }
            }
        }
    }
}
@media screen and (max-width:760px) {
    .Cart .Wrapper .Bottom{
        flex-direction: column;
    }
}
@media screen and (max-width:620px) {
    .Cart .Wrapper .Top
    .Cart .Wrapper .Top{
        padding: 10px 0px;
        flex-wrap: wrap;
    }
   .Toptexts{
       display: flex;
       flex-direction: column;
   }
}
@media screen and (max-width:470px) {
    .Cart .Wrapper{
        padding: 20px 0px;
    }
    .Cart .Wrapper .Top {
        padding: 5px 0px;
        flex-direction: column;
    }
    .Cart .Wrapper .Top .Topbutton{
        width: 100%;
    }
    .Toptexts{
        width: 100%;
        margin: 8px 0px;
    }
    .cProduct .productDetails img{
        width: 100%;
    }
    
    
}