.SingleProduct{
    
    .Wrapperu{
        padding: 50px;
        width: 100%;
        display: flex;
        .Imgcontainer{
            flex: 1;
         img{
             width: 90%;height: 75vh;
             object-fit: cover;
         }
        }
        .Infocontainer{
            flex: 1;
padding:0 50px;
            .Title{
                font-weight: 200;
                text-transform: capitalize;
            }
            .Desc{
                margin: 20px 0;
            }
            .price{
                font-weight: 100;
                font-size: 40px;
            }
            .filtercontainer{
                display: flex;
                width: 50%;
                margin: 30px 0;
                justify-content: space-between;
                .filter{
                    display: flex;
                    align-items: center;
                    flex-direction: column;
                    .filtertitle{
                        font-size: 23px;font-weight: 200;
                    }
                    .filtercolor{
                        display: flex;
                        .filtercolorbox{
                            width: 20px;height: 20px;border-radius: 50%;
                            background-color: #111;
                            margin-right: 5px;cursor: pointer;
                            &:nth-child(2){
                                background-color: green;
                            }
                            &:nth-child(3){
                                background-color: blue;
                            }
                        }

                    }
                    select{
                        padding: 5px;outline: none;
                    }
                }
            }
            .AddContainer{
                display: flex;
                align-items: center;
                width: 50%;
                justify-content: space-between;
                .amountcontainer{
                    display: flex;
                    align-items: center;font-weight: 700;
                    .amount{
                        width: 30px;
                        height: 30px;border-radius: 10px;
                        border: 1px solid teal;
                        display: flex;align-items: center;justify-content: center;margin:0 5px;
                    }
                }
                .addtocart{
                    border-radius: 5px;
                    font-size: 19px;cursor: pointer;
                    background-color: #fff;
                    font-weight: 500;
                    &:hover{
                        background-color: #f8f4f4;
                    }
                    text-transform: capitalize;
                    padding: 7px 15px;border: 1px solid teal;
                    outline: none;
                }
            }
        }
    }
}
@media only screen and (max-width:1000px) {
    .SingleProduct .Wrapperu .Imgcontainer img{
        height: 50vh;
    }
    .SingleProduct .Wrapperu .Infocontainer .AddContainer,
    .SingleProduct .Wrapperu .Infocontainer .filtercontainer{
        width: 70%;
    }
}
@media only screen and (max-width:750px) {
    .SingleProduct .Wrapperu .Imgcontainer img{
        height: 45vh;
    }
    .SingleProduct .Wrapperu .Infocontainer .AddContainer,
    .SingleProduct .Wrapperu .Infocontainer .filtercontainer{
        width: 90%;
    }
}
@media only screen and  (max-width:600px) {
    .SingleProduct .Wrapperu{
        padding: 20px 10px;
        flex-direction: column;
    }
    .SingleProduct .Wrapperu .Imgcontainer img{
        height: 30vh;
    }
}
@media screen and (max-width:380px) {
    .SingleProduct .Wrapperu .Infocontainer{
        padding: 0 10px;
    }
}