.Slider{
    width: 100%;
    height: 80vh;display: flex;
    // background-color: coral;
    overflow: hidden;
    position: relative;
    .Arrow{
        width: 50px;height: 50px;background-color: #f59999;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;cursor: pointer;
        opacity: 0.6;
        position: absolute;
        top: 0;bottom: 0;margin: auto;
        z-index: 2;
    }
    .Arrow:nth-child(2){
        right: 20px;
    }
    .Arrow:first-child{
        left: 20px;
    }
    .Arrow:hover{
        opacity: 1;
    }
    .Wrapper{
        height: 100%;
        display: flex;
        transition-delay: 0.2s;
        transition: all 1.5s cubic-bezier(0.175, 0.885, 0.32, 1.275);
        .Slide{
            width: 100vw;
            height: 100%;
    mix-blend-mode: lighten;
    display: flex;
    
            align-items: center;
            background-color: #F6F6F6;
            .ImgContainer{
    flex:1;
                height: 100%;
                img{
          height: 100%;
    width: 100%;
    object-position: 100% 0%;
    object-fit: cover;
                }
            }
            .InfoContainer{
                flex: 1;
    padding: 50px;
    .Title{
font-size: 70px;
    }
    .Button{
        padding: 10px;font-size: 20px;
        background-color:transparent;
        cursor: pointer;
    }
    .Desc{
        margin: 50px 0;
        font-size: 22px;font-weight: 500;letter-spacing: 3px;
    }
        }

        }
      
    }
}
@media only screen and (max-width:380px){
   .Slider{
       display: none;
   }
}