:any-link{
    text-decoration: none;
    color: inherit;
}
.container{
    height: 60px;
    background-color: #F6F6F6;

    .Wrapper{
        padding: 10px 20px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        .Left,
        .Center,
            .Right{
                flex: 1;
              }

        .Left{
            display: flex;
            align-items: center;
            .Language{
                font-size: 17px;cursor: pointer;
            }
            .Search-container{
                display: flex;
                align-items: center;border: 1px solid #111;
                margin-left: 25px;
                input[type="text"]{
                    border: none;
                    height: 30px;
                    background-color: transparent;
                    outline-width: 0;

                    
                }
            }
        }
        .Center{
            text-align: center;
            .Logo{
                font-weight: bold;
            }
           
        }
        .Right{
            display: flex;
            justify-content: flex-end;
            align-items: center; .MenuItem{
                font-size: 17px;
                cursor: pointer;
                margin-left: 25px;
            }

        }
    }
}

@media only screen and (max-width:545px) {
    .Announcement{
        font-size: 15px;
    }
    .container{
        height: 50px;
    }
    .container .Wrapper .Left .Search-container{
        border: 1px solid orange;
        width: 100px;
        margin-left: 10px;
        input{
            width: 100%;
        }
    }
   .container .Wrapper{
        padding: 10px 0px;
    }
    .Language{
        display: none;
    }
    .Center{
        display: none;
    }
    .container .Wrapper .Left{
        width: 100px;
    }
    .container .Wrapper .Right{
        justify-content: center;
    }
    .container .Wrapper .Right .MenuItem{
        margin-left: 10px;
    }
}
@media only screen and (max-width:340px) {
    .Announcement{
        font-size: 12px;
    }
    .Auth .Wrapper{
        width: 100%!important;
        padding: 10px 5px!important;
    }
    .Auth .Wrapper .form .create button{
        width: 100%!important;
    }
    .Auth .Wrapper .Title{
        font-size: 16px!important;
    }
    .container .Wrapper .Left .Search-container{
        border: 1px solid orange;
        width: 60px;
        margin-left: 10px;
        input{
            width: 100%;
        }
    }
}