:any-link{
    text-decoration: none;
}
.Auth{
    width: 100vw;
    height: 100vh;
    display: flex;align-items: center;justify-content: center;
    background: linear-gradient(rgba(255,255,255,0.5),
    rgba(255,255,255,0.5)), url('./log.jpg') no-repeat;
    background-size: cover; 
    .Wrapper{
        padding: 20px;
        width: 450px;
      display: flex;border-radius: 8px;
        flex-direction: column;
        background-color: white;

        .Title{
            font-size: 25px;
            font-weight: 300;
        }
        .form{
            align-items: center;
            display: flex;
            flex-direction: column;
            flex-wrap: wrap;

            input{
                width: 80%;
padding: 10px;outline: none;
                flex: 1;font-size: 17px;
                margin: 20px 10px 0 0;
            }
            .Agreement{
width: 80%;margin: 20px 0;
font-size: 12px;
&::first-letter{
    text-transform: uppercase;
}
            }
            .links{
                width: 80%;
                display: flex;justify-content: space-between;
margin:0 10px;
margin-bottom: 10px;

            }
            .create{
                width: 80%;
               button{
                   font-size: 18px;text-transform: capitalize;
                width: 29%;cursor: pointer;
                padding: 5px 20px;
                outline: none;
                border: none;
                background-color: teal;color:white;
               }
            }
        }
    }
}