.Productsitem{
    flex: 1;
    margin: 5px;
    height: 350px;display: flex;
       
        min-width: 280px;
        overflow: hidden;
        align-items: center;
        position: relative;
        justify-content: center;
        background-color: #f5fbfb;
    .okimg{
        height: 100%;
        object-fit: cover!important;
       z-index: 2;
    }
    &:hover .info{
        opacity: 1;
    }
    .info{
        opacity: 0;
        width: 100%;height: 100%;position: absolute;
        top: 0;left: 0;
        transition: all 1s ease;
        z-index: 3;display: flex;align-items: center;
        justify-content: center;
      
        background-color: rgba($color: #000000, $alpha: 0.2);
            .icon{
                width: 40px;height: 40px;
                cursor: pointer;transition: all 0.3s linear;
                border-radius: 50%;margin: 10px;
                &:hover{
                    background-color: #e9f5f5;
                    transform: scale(1.1);
                }
                background-color: #fff;
                display: flex;
                align-items: center;
                justify-content: center;
            }
        }
    .Circle{
        
        width: 250px;height: 250px;
        border-radius: 50%;
        background-color: rgb(248, 248, 248);
        position: absolute;
        
    }
}