.Footer{
    display: flex;
    align-items: center;
    justify-content: space-between;
    .left{flex: 1;
        display: flex;
        flex-direction: column;
        padding: 20px;

        img{width: 60px;height: 45px;}
        .Desc{
            margin: 20px 0;
        }
        .SocialContainer{
            display: flex;
            .socialicon{
                width: 40px;
                height: 40px;
                border-radius: 50%;cursor: pointer;
                display: flex;
                align-items: center;
                justify-content: center;
                margin-right: 20px;
                color: white;
                background-color: #3B5999;
                &:nth-child(2){
                    background-color: #E4405f;
                }
                &:nth-child(3){
                    background-color: #55ACEE;
                }
                &:nth-child(4){
                    background-color: #E60023;
                }
            }

        }
    
    }
    .center{flex: 1;

    padding: 20px;
    .Title{
        margin-bottom: 30px;
    }
    .list{
        margin: 0;padding: 0;
        list-style: none;
        display: flex;
        flex-wrap: wrap;
        .listitem{
width: 50%;
margin-bottom: 10px;
cursor: pointer;
        }
    }

}
    .right{
        flex: 1;
    padding: 20px;
    align-self: stretch;
     .Title{
        margin-bottom: 30px;

     }
     
     .contactitem{
         margin-bottom: 20px;
         display: flex;
         align-items: center;

     }
    }

}
@media screen and (max-width:890px) {
    .Footer{
        flex-wrap: wrap;
    }
    .Footer .right{
        flex: 100%;
    }
}
@media screen and (max-width:570px) {
    .Footer{
        flex-wrap: wrap;
    }
    .Footer .center{
        flex: 100%;
    }
}