.Newslater{
height: 60vh;background-color: #fcf5f5;
display: flex;
align-items: center;
justify-content: center;
flex-direction: column;

.Title{
    font-size: 70px;
    margin-bottom: 20px;
}
.Desc{
font-size: 24px;
font-weight: 300;
margin-bottom: 20px;
text-transform: capitalize;

}
.inputContainer{
    width: 50%;
    height: 40px;
    background-color: #fff;
    display: flex;justify-content: space-between;
    border: 1px solid lighgrey;
    input[type='text']{
        border: none;
        outline: none;
        flex: 8;font-size: 17px;
        padding-left: 20px;
    }
     .btn{
         flex: 1;
         cursor: pointer;
         border: none;
         background-color: teal;
         color: #fff;
     }
}
}
@media screen and (max-width:998px) {
    .Newslater{
        height: 50vh;
    }
    .Newslater .inputContainer{
        width: 70%;
    }
}
@media screen and (max-width:660px) {
    .Newslater .Title{
        font-size: 40px;
    }
    .Newslater .Desc{
        font-size: 19px;
    }
    .Newslater{
        height: 35vh;
    }
    .Newslater .inputContainer{
        width: 70%;
    }
}
@media screen and (max-width:410px) {
    .Newslater .Title{
        font-size: 30px;
    }
    .Newslater .Desc{
        font-size: 18px;
    }
    .Newslater{
        height: 30vh;
        align-items: flex-start;
        padding-left: 10px;
    }
    .Newslater .inputContainer{
        width: 90%;
    }
}