.Categoryitem{
    flex: 1;margin: 3px;
    position: relative;
    height: 70vh;
    img{
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
    .info{
        position: absolute;
        top: 0;width: 100%;height: 100%;
        left: 0;display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        .Title{
            color: white;font-weight: 700;
            margin-bottom: 20px;
        }
        .btn{
            border: none;
            padding: 10px;
            font-weight: 600;
            font-size: 17px;
            border-radius: 4px;
            background-color: white;
            color: grey;
            cursor: pointer;
        }
    }
}
@media screen  and (max-width:1000px){
    .Categories{
        flex-wrap: wrap;
    }
    .Categoryitem{
        flex: 45%;
        height: 45vh;
    }
}
@media screen  and (max-width:600px){
    .Categoryitem{
        flex: 50%;
        height: 35vh;
    }
}